import React from "react";
import styled from 'styled-components'
import {HeaderCore} from "../header/HeaderCore"


const StyledHeader = styled.div`
  background-color: hsl(236, 25%, 23%);
  position: relative;
  width: 100vw;
  height: 10vh;
`

export const Header = () => {
    return (
        <StyledHeader>
            <HeaderCore/>
        </StyledHeader>


    )
}